








































































































.result-wrapper {
	text-align: center;

	.result-title {
		margin: 0.2em 0;
		font-size: 2em;
		font-weight: 300;
	}
	.result-subtitle {
		margin: 0.2em 0;
		font-size: 1.2em;
		font-weight: 300;
	}
	.result-numbers {
		margin: 0.1em;
		font-size: 3em;
		font-weight: bold;
	}
	.result-image {
		width: 18em;
		margin: 1em 0;

		&.success-icon {
			width: 5em;
		}
	}
	.button-wrapper {
		display: flex;
		justify-content: center;
		margin: 2em 0;
	}
}
