



















































.title {
	margin: 2em 0 1em;
}
table,
th,
td {
	border-collapse: collapse;
	border: 1px solid black;
}

th,
td {
	padding: 8px;
}

td {
	overflow: hidden;
	text-overflow: ellipsis;
}

table {
	width: 100%;
	text-align: left;
	table-layout: fixed;
}
.text-center {
	text-align: center;
}
