














































































































table,
th,
td {
	border-collapse: collapse;
	border: 1px solid black;
	border-right: none;
	border-left: none;
}

th {
	text-align: left;
}

th,
td {
	padding: 8px;
}

table {
	width: 100%;
}

.icon-column {
	width: 50px;
	text-align: center;

	i {
		vertical-align: middle;
	}
}

.fixed {
	table-layout: fixed;
}
.field-description {
	margin: 0;
	font-size: 0.8em;
	color: #828282;
}
.required-flag {
	font-size: 0.8em;
	vertical-align: top;
}
